<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <h1>{{blog.content}}</h1>
    <p>作者：{{blog.author}}</p>
    <p>分类：</p>
    <ul>
      <li v-for="category in blog.categories">
        {{category}}
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "singleBlog",
    data() {
      return {
        id: this.$route.params.id,
        blog: {}
      }
    },
    created: function () {
      this.$http.get("http://jsonplaceholder.typicode.com/posts" + this.id + ".json")
        .then(function (data) {
          console.log(data);
          return data.json();
          //this.blog = response.body
        })
        .then(function (data) {
           this.blog = data;
        })
    }
  }
</script>

<style scoped>
  #single-blog {
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
  }
</style>



